.site-list {
  width: 100%;
  list-style: none;
  padding: 0;
  margin: 0;
}

@media (min-width: @tablet-width) {
  .site-list {
    width: 440px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
}

@media (min-width: @desktop-width) {
  .site-list {
    width: 481px;
  }
}

.site-list__item {
  box-sizing: border-box;
  padding-top: 21px;
  padding-bottom: 21px;
  text-align: center;
  border-top: 1px solid @extra-grey;
  margin-top: 2px;
}

@media (min-width: @tablet-width) {
  .site-list__item {
    padding-top: 0;
    padding-bottom: 0;
    text-align: left;
    border: none;
    margin-top: 0;
  }

  .site-list__item:first-child {
    margin-left: 0;
  }

  .site-list__item--current {
    padding-bottom: 8px;
    border-bottom: 2px solid @green;
  }
}

@media (min-width: @desktop-width) {
  .page-header--index .site-list__item--current {
    border-bottom: 2px solid @white;
    padding-bottom: 6px;
  }
}

.site-list__link {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  color: @black;
  text-transform: uppercase;
}

.site-list__link:hover {
  opacity: 0.6;
  cursor: pointer;
}

.site-list__link:active,
.site-list__link:focus {
  opacity: 0.3;
  cursor: pointer;
}

@media (min-width: @tablet-width) {
  .site-list__link {
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
}

@media (min-width: @desktop-width) {
  .site-list__link {
    font-size: 20px;
    line-height: 27px;
  }

  .page-header--index .site-list__link {
    color: @white;
  }
}
